import React, {Component} from "react";
import "./nav.css";
import styled from "styled-components";
import {Motion, spring} from "react-motion";
import {Link} from "react-router-dom";

const Navbar = styled.nav`
background: black;
position: absolute;
left: ${props => props.left}%;
top: 100%;
border: 2px solid black;
margin: 0;
z-index: 100;
width: 50%;
text-align: left;
`


class Nav extends Component {

  constructor(props) {

    super(props);

    this.state = {
      toggleMenu: 0,
    }

    }

    showMenu = () => {
      this.setState({
        toggleMenu: !this.state.toggleMenu,
      })
    }

    render() {
      return(
        <header>
          <h1 className="logo">Logo</h1>
          <input type="checkbox" id="nav-toggle" className="nav-toggle"/>
          <label htmlFor="nav-toggle" className="nav-toggle-label" onClick={this.showMenu}>
            <span></span>
          </label>
          <Motion defaultStyle={{left: -52}}
            style={{
              left: spring(this.state.toggleMenu ? 1 : -52, {
                stiffness: 210,
                damping: 15,
              }),
            }}>
            {style => (
              <Navbar left={style.left}>
                <ul>
                  <li><Link to="/about">About</Link></li>
                  <li><Link to="/projects">Projects</Link></li>
                </ul>
              </Navbar>
            )}
          </Motion>
        </header>
      )
    }
  }


export default Nav;



