import React from "react";
import "./landing.css";
import {Link} from "react-router-dom";



const Landing = () =>
    <div className="background">
      <div className="intro-overlay" />
    <section className="landing">
      <div className="header">
        <h2 className="hello">Hello World.</h2>
        <h1 className="second"> I'm <span className="name">Roar Petersen</span>.</h1>
        <h1 className="third"> A full-stack web development nerd.</h1>
      <Link to="/projects"><button>View my work</button></Link>
      </div>
    </section>
    </div>

export default Landing;



